import { Component, OnInit, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-bill',
  templateUrl: './bill.component.html',
  styleUrls: ['./bill.component.css']
})
export class BillComponent implements OnInit {

	bFName: string;
  bLName: string;
  bMail: string;
  bNumber: string;
  bAddress1: string;
  bAddress2: string;
  bCountry: string;
  bState: string;
  bCity: string;
  bZip: string;
  billAndShip: boolean;

	@Output() bComplete: EventEmitter<any> = new EventEmitter();

  constructor() { }

  ngOnInit() {
  }

  bill(){
    const bDetails = {
      bFName: this.bFName,
      bLName: this.bLName,
      bMail: this.bMail,
      bNumber: this.bNumber,
      bAddress1: this.bAddress1,
      bAddress2: this.bAddress2,
      bCountry: this.bCountry,
      bState: this.bState,
      bCity: this.bCity,
      bZip: this.bZip,
      billAndShip: this.billAndShip
    }
  	this.bComplete.emit(bDetails)
  }

}



