import { Component, OnInit } from '@angular/core';

import { ShopService } from '../../services/shop.service'

@Component({
  selector: 'app-store',
  templateUrl: './store.component.html',
  styleUrls: ['./store.component.css']
})
export class StoreComponent implements OnInit {

  pInCart: boolean;
  pSelect: boolean;
  isDisplayed = {};

  sProducts: {
  	pName: string,
  	pPrice: number,
  	pDes: string,
  	pImgs: string[]
  }[];

  p2Cart: {
    pName: string,
    pPrice: number,
    pQty: number,
//    pImg: string
  }[] = new Array();

  product: {
    pName: string,
    pPrice: number,
    pDes: string,
    pImgs: string[]
  };

  constructor(
    private shopS: ShopService
    ) { }

  ngOnInit() {
    this.pSelect = false;
    this.isDisplayed = {
      isDisplayed: false
    };
  	this.sProducts = [{
  		pName: 'Deodorant',
  		pPrice: 10,
  		pDes: 'Fresh and sweet',
  		pImgs: ['assets/deodorant1.jpg', 'assets/deodorant2.jpg', 'assets/deodorant3.jpg']
  	  	},{
  		pName: 'Soap',
  		pPrice: 8,
  		pDes: 'Clean up and smells good',
  		pImgs: ['assets/soap1.jpg', 'assets/soap2.jpg', 'assets/soap3.jpg']
  	  	},{
  		pName: 'Shave',
  		pPrice: 7,
  		pDes: 'Safe and sound',
  		pImgs: ['assets/shave1.jpg', 'assets/shave2.jpg', 'assets/shave3.jpg']
  	  	},{
  		pName: 'After Shave',
  		pPrice: 5,
  		pDes: 'Healty lotion',
  		pImgs: ['assets/ashave1.jpg', 'assets/ashave2.jpg', 'assets/ashave3.jpg']
  	  	},{
  		pName: 'Wet Towels',
  		pPrice: 4,
  		pDes: 'Be fresh everywhere, everytime',
  		pImgs: ['assets/wtowels1.jpg', 'assets/wtowels2.jpg', 'assets/wtowels3.jpg']
  	  	}]
  }

  pDisplay(pId){

    if(this.pSelect){

      this.cDisplay();

      setTimeout(()=>{
        this.isDisplayed = {
          isDisplayed: true
        }
        this.product = this.sProducts[pId];
        this.pSelect = true;
      }, 500)

    } else{

      this.isDisplayed = {
        isDisplayed: true
      }
        this.product = this.sProducts[pId];
        this.pSelect = true;

    }


  }

  cDisplay(){
   this.isDisplayed = {
      isDisplayed: false
    }
  }
  
  add2Cart(product: any){
      var cIndex;

      var cProduct = {
        pName:this.product.pName,
        pPrice: this.product.pPrice,
        pQty: product.pQty,
        //pImg: this.product.pImgs[0]        
      }

      this.pInCart = false;

      for (let i in this.p2Cart) {
        if(this.p2Cart[i].pName == cProduct.pName){
          cIndex = i;
          this.pInCart = true;
        } 
      }     

      if(this.pInCart){
        this.p2Cart[cIndex].pPrice = cProduct.pPrice;
        //this.p2Cart[cIndex].pImgs = cProduct.pImg;
        this.p2Cart[cIndex].pQty = cProduct.pQty;
      } else{
        this.p2Cart.push(cProduct);
      }

      var toCart = this.p2Cart
      
      this.shopS.storeCProducts(toCart);

  }


}



