import { Component, OnInit, DoCheck, NgZone, Output, EventEmitter, Input } from '@angular/core';

import { LogService } from '../../services/log.service'

import { ValiService } from '../../../main/services/validate.service'

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  
  uUser: String;
  uPass: String;
  showLog: {};
  @Input() isShown: boolean;

  @Output() cLogin: EventEmitter<boolean> = new EventEmitter();


  constructor(
    private logS: LogService,
  	private ngZone: NgZone,
    private valiS: ValiService

  ) { }

  ngOnInit(){

  }

  ngDoCheck() {
      this.showLog ={
        showLog: this.isShown
    }
  }

  logUser(){
  	const user = {
  		username: this.uUser,
  		password: this.uPass
  	}
    // Required fields
    if(!this.valiS.validateLogin(user)){
      console.log("Fill in all fields")
      return false;
    }


    this.ngZone.run(()=> {
      this.logS.authenticateUser(user).subscribe(data => {
        if(data.success){
          this.logS.storeUserData(data.token, data.roleToken, data.user);
          this.closeLogin();
        } else{
          return false;
        }
      });
    });
  }

  closeLogin(){
      this.cLogin.emit(false);
  }
}





