import { Component, OnInit, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-ship',
  templateUrl: './ship.component.html',
  styleUrls: ['./ship.component.css']
})
export class ShipComponent implements OnInit {

  sFName: string;
  sLName: string;
  sMail: string;
  sNumber: string;
  sAddress1: string;
  sAddress2: string;
  sCountry: string;
  sState: string;
  sCity: string;
  sZip: string;

	@Output() sComplete: EventEmitter<any> = new EventEmitter();

  constructor() { }

  ngOnInit() {
  }

  ship(){
    const sDetails = {
      sFName: this.sFName,
      sLName: this.sLName,
      sMail: this.sMail,
      sNumber: this.sNumber,
      sAddress1: this.sAddress1,
      sAddress2: this.sAddress2,
      sCountry: this.sCountry,
      sState: this.sState,
      sCity: this.sCity,
      sZip: this.sZip
    }

  	this.sComplete.emit(sDetails)
  }

}



