import { Component, OnInit, Input, Output, EventEmitter, NgZone } from '@angular/core';
import {Router} from '@angular/router'

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnInit {

  aBox: {}; 
  aMenu: {};
  aItem: {};

  aLogin: {};
  aCart: {};
  aRegister: {};

  opBars: {};
  opClose: {};  

  boxOn: boolean;
  menuOn: boolean;

  dLogin: boolean;
  dRegister: boolean;
  dCart: boolean;

@Input() uLogged: boolean;

@Output() tLogout: EventEmitter<boolean> = new EventEmitter();

@Output() boxOpen: EventEmitter<boolean> = new EventEmitter();


  constructor(
  	private router:Router,
  	private ngZone: NgZone
  	) {}

  ngOnInit() {
    this.menuOn = false;
    this.dLogin = false;
    this.dRegister = false;
    this.dCart = false;
    this.boxOn = false;

    this.aBox = {
      aBox: false
    };
    this.aMenu = {
      aMenu: false,
      aBox: false
    };
    this.aItem = {
      aItem: false
    };

    this.opBars = {
      op0: false,
      op1: true 
    };

    this.opClose = {
      op0: true,
      op1: false 
    };

    this.aLogin = {
      aDisp: false
    }
    this.aRegister = {
      aDisp: false
    }
    this.aCart = {
      aDisp: false
    }
  }

  logOut(){
    this.tLogout.emit(true);
    this.router.navigate(['/']);
  }

  openRegister(){

    this.dRegister = !this.dRegister;

    if(this.dRegister){
      if(!this.boxOn){
        this.tBox();
      } else{
        this.aLogin = {
          aDisp: false
        }

        this.aCart = {
          aDisp: false
        }
      }      
      setTimeout(() =>{
        this.dCart = false;
        this.dLogin = false;    
        this.aRegister = {
          aDisp:  true
        }
      }, 500)
    } else {
        this.aRegister = {
          aDisp:  false
        }
         this.opBars = {
          op0: false,
          op1: true
        }      
        this.opClose = {
          op0: true,
          op1: false
        }     
       
        this.tBox();
      }
  }

  openLogin(){

    this.dLogin = !this.dLogin;
       if(this.dLogin){
      if(!this.boxOn){
        this.tBox();
      } else{
        this.aRegister = {
          aDisp: false
        }

        this.aCart = {
          aDisp: false
        }
      }

      setTimeout(() =>{
        this.dCart = false;
        this.dRegister = false;
        this.aLogin = {
          aDisp:  true
        }
      }, 500)
    } else {
        this.aLogin = {
          aDisp:  false
        }
        this.opBars = {
          op0: false,
          op1: true
        }      
        this.opClose = {
          op0: true,
          op1: false
        }     
       
        this.tBox();
      }
  }


  openCart(){

    this.dCart = !this.dCart;
    if(this.dCart){
      if(!this.boxOn){
        this.tBox();
      } else{
        this.aLogin = {
          aDisp: false
        }

        this.aRegister = {
          aDisp: false
        }
      }      
      setTimeout(() =>{
        this.dRegister = false;
        this.dLogin = false;
        this.aCart = {
          aDisp:  true
        }
      }, 500)
    } else {
        this.aCart = {
          aDisp:  false
        }
        this.opBars = {
          op0: false,
          op1: true
        }      
        this.opClose = {
          op0: true,
          op1: false
        }     
       
        this.tBox();
    }
  }

  tMenu(){

      this.menuOn = !this.menuOn
    
      if(this.boxOn){

        this.opBars = {
          op0: false,
          op1: true
        }      
        this.opClose = {
          op0: true,
          op1: false
        }     
        this.tBox()      
      }else {

        if(this.menuOn){
          this.opBars = {
            op0: true,
            op1: false
          }      
          this.opClose = {
            op0: false,
            op1: true
          }          
        } else{
          this.dCart = false;
          this.dLogin = false;
          this.dRegister = false;
            this.aLogin = {
          aDisp: false
        }

        this.aRegister = {
          aDisp: false
        }
      
        this.aCart = {
          aDisp: false
        }
          this.opBars = {
            op0: false,
            op1: true
          }      
          this.opClose = {
            op0: true,
            op1: false
          }

      }

    }

    this.aMenu ={
        aMenu: this.menuOn,
        aBox: this.boxOn
    }
    this.aItem ={
        aItem: this.menuOn
    }
  }

  tBox(){

    this.boxOn = !this.boxOn
    if(!this.boxOn){
      this.dCart = false;
      this.dLogin = false;
      this.dRegister = false;
        this.aLogin = {
          aDisp: false
        }

        this.aRegister = {
          aDisp: false
        }
      
        this.aCart = {
          aDisp: false
        }

      this.menuOn = true;
      this.tMenu();   
    } else{
      this.opBars = {
        op0: true,
        op1: false
      }      
      this.opClose = {
        op0: false,
        op1: true
      }
    }
      this.boxOpen.emit(this.boxOn);     

      this.aMenu = {
        aMenu: this.menuOn,
        aBox: this.boxOn
      }
            
      this.aBox = {
        aBox: this.boxOn
      }
  }

}



