import { Injectable, NgZone } from '@angular/core';
import {Http, Headers} from '@angular/http';
import 'rxjs/add/operator/map';
import {tokenNotExpired} from 'angular2-jwt';
import { Subject }    from 'rxjs/Subject';

/*
  Generated class for the LogService provider.

  See https://angular.io/docs/ts/latest/guide/dependency-injection.html
  for more info on providers and Angular 2 DI.
*/
@Injectable()
export class LogService {
    // Observable string sources
  private uInfo = new Subject<any>();
  private uToken = new Subject<string>();

  authToken: any;
  roleToken: any;
  user: any;

  testSource = 'http://localhost:3000';
  prodSource = '';
  address = '/users';

  constructor(
    private http:Http
  ) { }

  registerUser(user){
    let headers = new Headers();
    headers.append('Content-Type','application/json');
    return this.http.post(this.prodSource+this.address+'/cUser', user, {headers: headers})
      .map(res => res.json());
  }

  authenticateUser(user){
  
    let headers = new Headers();
    headers.append('Content-Type','application/json');
    console.log("Im here, mate")
    return this.http.post(this.prodSource+this.address+'/authUser', user, {headers: headers})
      .map(res => res.json());
  }
  
  getProfile(){
    let headers = new Headers();
    const token = this.getToken();
    headers.append('Authorization', token);
    headers.append('Content-Type','application/json');
    return this.http.get(this.prodSource+this.address+'/gUser', {headers: headers})
      .map(res => res.json());    
  }

  getToken(){
    const token = localStorage.getItem('id_token');
    this.authToken = token;
    return this.authToken
  }
    


  storeUserData(token, roleToken, user){
      localStorage.setItem('id_token', token);
      localStorage.setItem('role', roleToken);
      localStorage.setItem('user', JSON.stringify(user));
      this.authToken = token;
      this.roleToken = roleToken;
      this.user = user;
  }

  loggedIn(){
    return tokenNotExpired();
  }

  logout(){
    this.authToken = null;
    this.roleToken = null;
    this.user = null;
    localStorage.clear();
  }



}



