import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

import { PayService } from '../../services/pay.service';

@Component({
  selector: 'app-pay',
  templateUrl: './pay.component.html',
  styleUrls: ['./pay.component.css']
})
export class PayComponent implements OnInit {

  currency: string;

  cName: string;
  cNumber: string;
  cMonth: string;
  cYear: string;
  cCode: string;

  checkToken: string
  cardId: string;
  chargeId: string;

  @Input() subTotal: number;

  @Input() description: any;

	@Output() pComplete: EventEmitter<any> = new EventEmitter();

  constructor(
    private payS: PayService
  ){}

  ngOnInit() {
    this.currency = 'usd'
  }

  pay(){
    (<any>window).Stripe.card.createToken({
      number: this.cNumber,
      exp_month: this.cMonth,
      exp_year: this.cYear,
      cvc: this.cCode
    }, (status: number, response: any) => {

      // Wrapping inside the Angular zone
        if (status === 200) {
          this.cardId = response.card.id;
          this.checkToken = response.id
          this.doPayment();
        } else {
          console.log(response.error.message);
        }
    });

  }

  doPayment(){
    const payment = {
      amount: this.subTotal,
      currency: this.currency,
      source: this.checkToken,
      description: this.description
    }

    this.payS.stripeCharge(payment, false).subscribe(data => {
      if(data.success){
        this.chargeId = data.chargeId;
        const pDetails = {
          cardId: this.cardId,
          chargeId: this.chargeId,
          checkToken: this.checkToken
        }
        console.log(pDetails); 
      	this.pComplete.emit(pDetails)
      } else{
        console.log(data.msg);
      }
    });

  }

}



