import { Component, OnInit, Output, EventEmitter } from '@angular/core';

import { LogService } from '../../services/log.service'

import { ValiService } from '../../../main/services/validate.service'

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

	uFName: String;
  uLName: String;
	uMail: String;
	uRepass: String;	
 	uUser: String;
  uPass: String;
  uRole: String;


@Output() cRegister: EventEmitter<boolean> = new EventEmitter();

  constructor(
  	private logS: LogService,
  	private valiS: ValiService
  ) { }

  ngOnInit() {
    this.uRole = 'hMan'
  }

  regUser(){
  	const user={
  		fName: this.uFName,
      lName: this.uLName,      
  		username: this.uUser,
  		email: this.uMail,
  		password: this.uPass,
  		repassword: this.uRepass,
      role: this.uRole
    }

    // Required fields
    if(!this.valiS.validateRegister(user)){
    	console.log("Fill in all fields")
    	return false;
    }

    // Validate email
    if(!this.valiS.validateEmail(user.email)){
      console.log("Invalid email");
      return false;
    }

    if(!this.valiS.validateLength(user.password)){
      console.log("Invalid email");
      return false;
    }

    if(!this.valiS.validateMatch(user.password, user.repassword)){
      console.log("Invalid email");
      return false;
    }


    // Register user
    this.logS.registerUser(user).subscribe(data => {
      if(data.success){
        console.log("User Registered");
        this.closeRegister();
      } else{
        console.log("There was a promblem: "+data.msg);
        return false;
      }
    });
  }

  closeRegister(){
    this.cRegister.emit(false);
  }

}



