import { Component, OnInit, DoCheck } from '@angular/core';

import { LogService } from '../user/services/log.service';

@Component({
  selector: 'main-root',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.css']
})
export class MainComponent {

	onNav: {};
	dLogin: boolean;
	dRegister: boolean;
	uLogged: boolean;
	noClose: boolean;

	constructor(
	    private logS: LogService
	){}

	ngOnInit(){
		this.dLogin = false;
		this.dRegister = false;
		this.uLogged = false;
		this.onNav = {
			onNav: false
		}
	}

	ngDoCheck(){
		this.uLogged = this.logS.loggedIn()
	}

	tBody(tBody){
		this.onNav = {
			onNav: tBody
		}
	}

	openLogout(dLogout){
		if(dLogout){
			this.logS.logout();
		}
	}


}



