import { Component, Input, OnInit, NgZone, DoCheck} from '@angular/core';
import { Router } from '@angular/router';
import { ShopService } from '../../services/shop.service';

@Component({
  selector: 'app-cart',
  templateUrl: './cart.component.html',
  styleUrls: ['./cart.component.css']
})

export class CartComponent implements OnInit {

	cartFilled: boolean;

	cProducts: {
		pName: string,
		pPrice: number,
		pQty: number,
		pImg: string
	}[];

	subTotal: number;


  constructor(
  	private shopS: ShopService,
  	private ngZone: NgZone,
  	private router: Router
	) { }

	ngOnInit() {
		var products = this.shopS.getCProducts();
		if(products){
			this.cartFilled = true;
			this.cProducts = products;
		} else{
			this.cartFilled = false;
		}
	}

	ngDoCheck(){
		var products = this.shopS.getCProducts();
		if(this.cProducts){
			this.cartFilled = true;
			this.cProducts = products;
			this.subTotal = 0;
			for(let product of this.cProducts){
				this.subTotal += product.pQty*product.pPrice
			}
		} else{
			this.cartFilled = false;
		}
	}

	openCheck(){
		const subtotal = this.subTotal;
		var description: any[] = new Array();
		
		var aux: any = {
			pName: '',
			pPrice: 0,
			pQty: 0
		};		
				
		for( let i in this.cProducts){
			aux.pName = this.cProducts[i].pName;
			aux.pPrice = this.cProducts[i].pPrice;
			aux.pQty = this.cProducts[i].pQty;						
		
			description.push(aux);
		}

		this.router.navigate(['/checkout', subtotal, JSON.stringify(description)])
	}
}



