import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';

@Component({
  selector: 'app-checkout',
  templateUrl: './checkout.component.html',
  styleUrls: ['./checkout.component.css']
})
export class CheckoutComponent implements OnInit {

	subTotal: number;
  description: any;

  aDetails: any;
  bDetails: any;
  sDetails: any;
  pDetails: any;

	tBilling = {};
	tShipping = {};
	tPayment = {};

  dBilling: boolean;
  dShipping: boolean;
  dPayment: boolean;

  constructor(
  	private route: ActivatedRoute
  ){}

  ngOnInit() {

    this.dBilling = false;
   
    this.dShipping = false;
    
    this.dPayment = false;
    

  	this.tBilling = {
  		tBox: false
  	}
  	this.tShipping = {
  		tBox: false
  	}
  	this.tPayment = {
  		tBox: false
  	}
	this.route.params
  		.subscribe(params => {
        this.subTotal = params['subtotal'];
        this.description = params['description'];
      });
  	console.log(this.subTotal);
  }

  tBill(aDetails){
    this.aDetails = aDetails;
    this.tBilling = {
      tBox: true
    }
    setTimeout(() => {
      this.dBilling = true;
    }, 230)
  }

  tShip(bDetails){
    this.bDetails = bDetails;
    
    this.tShipping = {
      tBox: true
    }    
    setTimeout(() => {
      this.dShipping = true;
    }, 230)
    
    if(bDetails.billAndShip){
      this.tPay(bDetails);
    }
  }

  tPay(sDetails){
    this.sDetails = sDetails;
    this.tPayment = {
      tBox: true
    }    
    setTimeout(() => {
      this.dPayment = true;
    }, 230)
  }

  endCheck(pDetails){
    this.pDetails = pDetails
    
  }

}



